:root {
  --primary-color: #1877F2;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::after {
  box-sizing: border-box;
}
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.text-center {
  text-align: center;
}

.container {
  padding: 1rem;
}
nav {
    display: flex;
    height: 4rem;
    justify-content: space-between;
    padding: 0 1rem;
    transition: .3s;
}
nav .brand {
    display: flex;
    align-items: center;
    color: #fff;
    text-decoration: none;

}
nav .brand img {
    height: 3rem;
    width: 3rem;
    /* margin-right: .5rem; */
}
nav .brand h1 {
    font-size: 1rem;
}

nav .nav-menu {
    display: flex;
    align-items: center;
}
nav .nav-menu .nav-menu-item {
    margin-right: 1rem;
}
nav .nav-menu .nav-menu-item:last-child {
    margin-right: 0;
}
nav .nav-menu .nav-menu-item span {
    cursor: pointer;
    color: #fff;
}
nav .nav-menu .nav-menu-item a {
    text-decoration: none;
    color: #fff;
}
.auth-container {
    display: flex;
    justify-content: center;
    margin-top: 4rem;
}

.card .card-title {
    margin-bottom: .5rem;
    text-align: center;
}

.form-element {
    margin: .5rem 0;
    width: 100%;
}
.form-element label {
    display: block;
    margin: .15rem 0;
    font-size: 1rem;
}
.form-element input {
    display: block;
    font-weight: bold;
    padding: .5rem;
    border-radius: .15rem;
    border: 2px solid #000;
}
.form-element input::-webkit-input-placeholder {
    font-weight: normal;
}
.form-element input::placeholder {
    font-weight: normal;
}

@media only screen and (min-width: 768px) {
    .form-element {
        min-width: 12rem;
    }
  }
.btn-primary {
    border: 0;
    padding: .5rem 1.5rem;
    border-radius: .15rem;
    color: #fff;
    cursor: pointer;
}

.btn-primary:hover {
    opacity: .9;
}

.btn-primary:active {
    opacity: .899;
}

.btn-primary:disabled {
    opacity: .6;
    cursor: not-allowed;
}
.settings-item {
    margin: .25rem 0;
}
.settings-item h3 {
    margin: .5rem 0;
    font-size: 1rem;
    font-weight: normal;
}

.settings-item-content {
    display: flex;
    flex-wrap: wrap;
}

.color-palette {
    height: 3rem;
    width: 3rem;
    border: 0;
    margin-right: .25rem;
    border-radius: 50%;
    cursor: pointer;
}
.color-palette:hover {
    opacity: .9;
}
.color-palette.active {
    border: 2px solid #222;
}
